document.addEventListener('DOMContentLoaded', function(){
  iniciarApp();

});

function iniciarApp() {
  eventListeners();
  // hojaJS2();

}
//Variables globales--
const pantallaCompleta = document.querySelector(".pantallaCompleta");
const body = document.querySelector(".body");
const menuBtn = document.querySelector("#menu-toggle");
const nav = document.querySelector(".nav");

const iconosModo = document.querySelector(".btn-modo-oscuro");




//Eventos de clicks
function eventListeners() {

  menuBtn.addEventListener('click', navegacion);
  pantallaCompleta.addEventListener('click', pantalla);

  //Modo oscuro
  toggleModo();
  // darkMode();

  //Se ejecuta este codigo si esta en pagina /contacto
  var pathname=window.location.pathname;
  if(pathname === '/contacto'){

    //Formulario checkbox
    const metodoContacto = document.querySelectorAll('input[name="contacto[sitio-cuentas]"]');
    metodoContacto.forEach(input => input.addEventListener('click', seleccionarMetodo));

    // Añdade la validacion del fomulario
    const btnformulario = document.querySelector(".btn-formulario");
    btnformulario.addEventListener('click', anadirValidacion);


    
  }

  //solo curriculum publico
  const curriculumPaginaPublica = document.querySelector('#curriculum-pagina-publica');
  if(curriculumPaginaPublica) {
    consultarAPIpublica();

  }



  

}

function navegacion() {

    menuBtn.classList.toggle("open");
    nav.classList.toggle("muestra");
    body.classList.toggle("fijar-body");
    pantallaCompleta.classList.toggle("activarPantallaNegra");
    
}

function pantalla() {

  menuBtn.classList.remove("open");
  nav.classList.remove("muestra");
  body.classList.remove("fijar-body");
  pantallaCompleta.classList.remove("activarPantallaNegra");
  
  const formulario = document.querySelector('.modal-formulario');
  if(formulario) {
    formulario.classList.add('cerrar');//esto hace animacion de cerrar modal

    setTimeout(() => {
        // alert('hola')
        const modal = document.querySelector('.modal-formulario');
        modal.remove();//remueve ese inerthtml despues de .5 segundos
    }, 500);//se ejecuta la animacion para animar el formulario, se ejecuta en .5 segundo
  }
 
}


