const toggleModo = () => {
  iconosModo.addEventListener("click", () => {
      
      body.classList.toggle("dark");
      document.querySelector(".sun-logo").classList.toggle("animate-sun");
      document.querySelector(".moon-logo").classList.toggle("animate-moon");


      if (!body.classList.contains("dark")) {
          localStorage.setItem("modo", "claro");
          //mustra el sol (modo claro)
          removerAnimacionDark();

      } else {
          localStorage.setItem("modo", "dark");
          //mustra la luna (modo oscuro)
          añadirAnimacionDark();

      }
  });

  let getModo = localStorage.getItem("modo");
  if (getModo === "dark") {
      
      body.classList.toggle("dark");
      añadirAnimacionDark();


  } else {
      
      body.classList.remove("dark");
      removerAnimacionDark();

  }
};


//Animacion del modo oscuro boton
function removerAnimacionDark() {
  document.querySelector(".moon-logo").classList.remove("animate-moon");
  document.querySelector(".sun-logo").classList.remove("animate-sun");
}

function añadirAnimacionDark() {
  document.querySelector(".moon-logo").classList.add("animate-moon");
  document.querySelector(".sun-logo").classList.add("animate-sun");
}

function anadirValidacion() {
  const formulario = document.querySelector(".formulario");
  formulario.classList.add("anadir-validacion");
}





//Loader de la pagina
window.addEventListener("load", () => {
  const loader = document.querySelector('.loader-fondo');
  loader.style.opacity = 0;
  loader.style.visibility = "hidden";
  body.classList.remove('fijar-body');

  setTimeout(function(){
    loader.remove();
  }, 1200);

});



